/*********************  Main containers ******************/

.main-container-movie {
    width: 100%;
    position: relative;
}

.movie-detail {
    min-height: 500px;
    margin-bottom: 10px;
}

.movie-detail .container-cover {
    min-width: 100%;
    min-height: 100%;
    position: absolute;
    z-index: -1;
    opacity: 0.4;
    background: no-repeat center center fixed ;
    background-size: cover;
}

/*********************  Movie details information ******************/
.movie-detail .main-title {
    width: 100%;
    color: #ffffff;
    text-shadow: 1px 2px black;
    font-weight: bold;
    padding-top: 20px;
    padding-left: 5px;
    font-size: 20px;
}

.movie-details {
    max-width: calc(48% - 40px);
    color: white;
    padding: 0 20px;
    float: right;
}

.cover-preview {
    min-width: calc(52%);
    height: 380px;
    margin-top: 30px;
    display: inline-block;
    cursor: pointer;
    -webkit-box-shadow: 0 1px 10px rgba(34, 34, 34, 0.8);
            box-shadow: 0 1px 10px rgba(34, 34, 34, 0.8);

    background: no-repeat center center ;
}


.movie-details .title {
    font-weight: bold;
    font-size: 16px
}

.movie-details .movie-description {
    font-weight: normal;
    font-size: 14px
}

.movie-details .genre {
    font-weight: normal;
    font-size: 16px;
}

.person-details {
    font-weight: normal;
    font-size: 16px;
    line-height: 26px;
}

.person-details .item {
    border: 1px solid #eeeeee;
    font-size: 12px;
    padding: 4px;
    margin: 3px;
    border-radius: 5px;
    cursor: pointer;
}

.person-details .item:hover {
    -webkit-box-shadow: 0 3px 4px rgba(123, 121, 127, 0.67);
            box-shadow: 0 3px 4px rgba(123, 121, 127, 0.67);
}

.social-icons {
    height: 80px;
}

.social-icons .item {
    height: 70px;
    width: 70px;
    text-align: center;
    display: inline-block;
    margin: 0 5px;
    cursor: pointer;
}

.social-icons .item .icon {
    line-height: 60px;
    font-size: 20px;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
    border: 1px solid #eee;
    padding: 10px;
    border-radius: 50%;
    display: block;
    margin: 0 auto;
}

.social-icons .item .icon:hover {
    -webkit-box-shadow: 0 4px 10px rgba(123, 121, 127, 0.67);
            box-shadow: 0 4px 10px rgba(123, 121, 127, 0.67);
}

.social-icons .item .text {
    line-height: 20px;
    font-size: 14px;
}



@media (max-width: 900px) {
    .container-cover {
    }

    .cover-preview {
        width: 100%;
        background-size: 100%;
        height: 220px;
    }

    .movie-details{
        max-width: calc(100% - 20px);
        position: relative;
        height: auto;
        float: none;
    }
}


