.input-search {
    height: 50px;
    line-height: 50px;
    text-align: right;
    padding-right: 35px;
}

.input-search input {
    vertical-align: middle;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-color: #343434;
    color: #d6d6d6;
    padding-left: 10px;
    border-radius: 5px 0 0 5px;
    min-width: 200px;
    height: 35px;
    border: 1px solid #6B6B6B;
    border-right: none;
}

.input-search .btn-container {
    vertical-align: middle;
    height: 35px;
    display: inline-block;
}


.input-search input:focus {
    border: 1px solid #6B6B6B;
    border-right: none;
    -webkit-box-shadow: none;
            box-shadow: none;
    outline: none;
}

.input-search ::-webkit-input-placeholder {
    color: #6B6B6B;
    opacity: 1;
}

.input-search :-ms-input-placeholder {
    color: #6B6B6B;
    opacity: 1;
}

.input-search ::-ms-input-placeholder {
    color: #6B6B6B;
    opacity: 1;
}

.input-search ::placeholder {
    color: #6B6B6B;
    opacity: 1;
}
.input-search :-ms-input-placeholder {
    color: #6B6B6B;
}
.input-search ::-ms-input-placeholder {
    color: #6B6B6B;
}


.input-search .btn-search {
    background-color: #343434;
    border: 1px solid #6B6B6B;
    color: #6B6B6B;
    border-radius: 0 5px 5px 0;
    height: 35px;
    width: 35px;
    border-left: none;
    position: absolute;
    padding: 5px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.input-search .btn-search:hover {
    cursor: pointer;
    background-color: #242424;
}

@media (max-width: 600px) {
    .input-search input {
        width: calc(100% - 110px);
    }
    .input-search {
        text-align: center;
    }
}


